import { ComponentChildren, JSX, Ref } from 'preact';
import { useFormContext } from '../hooks/UNSAFE_useFormContext';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';
import { textInterpolations } from '../utils/UNSAFE_interpolations/text';
import type { TextProps } from '../utils/UNSAFE_interpolations/text';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';
import { useFormFieldContext } from '../hooks/UNSAFE_useFormFieldContext';
import { getLoadingAriaLabel } from './FormControlUtils';

type AutoFocusProp = JSX.HTMLAttributes<HTMLInputElement>['autofocus'];

type ReadonlyAsInputProps = {
  as: 'input';
  type: JSX.HTMLAttributes<HTMLInputElement>['type'];
  elementRef?: Ref<HTMLInputElement>;
  rows?: never;
};

type ReadonlyAsTextAreaProps = {
  as: 'textarea';
  elementRef?: Ref<HTMLTextAreaElement>;
  type?: never;
  rows?: number;
};

type ReadonlyAsDivProps = {
  as?: 'div';
  elementRef?: Ref<HTMLDivElement>;
  type?: never;
  rows?: never;
};

type Props = TextProps &
  (ReadonlyAsDivProps | ReadonlyAsInputProps | ReadonlyAsTextAreaProps) & {
    ariaLabel?: string;
    ariaLabelledby?: string;
    autoFocus?: AutoFocusProp;
    hasInsideLabel?: boolean;
    id?: string;
    inlineUserAssistance?: ComponentChildren;
    innerReadonlyField?: ComponentChildren;
    value?: string;
  };

// In readonly textarea, we need some margin between the border container element
// and the textarea, so the the textarea's focus outline doesn't get truncated.
// The 0.25rem is an aesthetically pleasing amount of margin to make the readonly
// focus ring visible.
const borderToTextAreaContentMargin = '0.25rem';

const readOnlyTextFieldStyles = {
  base: cssProps`${{
    minHeight:
      'calc(var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height) * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-font-size))',
    // TODO: Use Text component instead of CSS
    wordBreak: 'break-word',
    whiteSpace: 'pre-wrap',
    // TODO: Cleanup the border and background CSS
    backgroundColor: 'transparent',
    borderColor: 'transparent',
    borderWidth: '0',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-primary)',
    boxSizing: 'border-box',
    // TODO: Use the Flex component instead of CSS
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    fontFamily: 'inherit',
    fontSize: '100%',
    fontWeight: 'inherit',
    lineHeight: 'inherit',
    outlineWidth: '0',

    '&:focus': {
      outlineStyle: 'dotted',
      outlineWidth: '1px',
      outlineColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-focus-border-color)',
      outlineOffset: '1px'
    }
  }}`,
  textareaBase: cssProps`${{
    resize: 'none',
    paddingBlockStart: '0',
    paddingInlineStart: '0'
  }}`,
  inEnabledFormBase: cssProps`${{
    whiteSpace: 'nowrap',
    overflowX: 'scroll',
    overflowY: 'hidden',
    scrollbarWidth: 'none'
  }}`,

  textareaInEnabledFormBase: cssProps`${{
    marginTop:
      'calc((var(--oj-c-PRIVATE-DO-NOT-USE-text-field-height) - 1rem * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height))/ 2 - 1px)',
    marginBottom:
      'calc((var(--oj-c-PRIVATE-DO-NOT-USE-text-field-height) - 1rem * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height))/ 2 - 1px)',
    marginLeft: borderToTextAreaContentMargin,
    marginRight: borderToTextAreaContentMargin,
    paddingLeft: `calc(var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-to-edge-padding) - ${borderToTextAreaContentMargin})`,
    paddingRight: '0',
    overflowX: 'auto',
    overflowY: 'auto',
    whiteSpace: 'pre-wrap'
  }}`,

  inEnabledFormLabelInside: cssProps`${{
    // TODO: Look into why we need this hacky CSS
    marginTop:
      'calc(var(--oj-c-PRIVATE-DO-NOT-USE-text-field-inside-edge-padding-top) + 0.1875rem)',
    paddingTop: '0',
    minHeight:
      'calc(var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height) * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-font-size))'
  }}`,

  textareaInEnabledFormLabelInside: cssProps`${{
    paddingTop: 0,
    paddingBottom: 0,
    // We are using a transparent border here instead of padding, due to how textarea scrolls the padding off the screen
    // and the text content will clash with the inside label.
    borderColor: 'transparent',
    borderStyle: 'solid',
    marginTop:
      'calc(var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-font-size) * var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-line-height) + var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-position-top))',
    marginBottom: borderToTextAreaContentMargin
  }}`,

  inEnabledFormNoLabelInside: cssProps`${{
    // TODO: Remove if this is not needed.
    justifyContent: 'center'
  }}`,

  // don't apply this for textarea
  inEnabledFormNoStartContent: cssProps`${{
    marginLeft: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-to-edge-padding)',
    marginRight: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-to-edge-padding)',
    paddingLeft: '0',
    paddingRight: '0'
  }}`
};

export function ReadonlyTextFieldInput({
  ariaLabel,
  ariaLabelledby,
  autoFocus,
  as = 'div',
  elementRef,
  hasInsideLabel = false,
  id,
  inlineUserAssistance,
  innerReadonlyField,
  rows,
  type,
  value = '',
  ...props
}: Props) {
  const { isFormLayout, isReadonly: isReadonlyForm } = useFormContext();
  const { isLoading } = useFormFieldContext();
  const ariaLabelForReadonly = isLoading ? getLoadingAriaLabel() : ariaLabel;

  // get classes for text align
  const interpolations = [...Object.values(textInterpolations)];
  const styleInterpolations = mergeInterpolations<Props>(interpolations);
  const { class: styleInterpolationClasses } = styleInterpolations(props);

  const readonlyDivClasses = classNames([
    as !== 'textarea' && 'oj-c-hide-scrollbar', // TODO: Temp workaround for using --webkit-scrollbar to hide the scrollbar
    readOnlyTextFieldStyles.base,
    as === 'textarea' && readOnlyTextFieldStyles.textareaBase,
    isFormLayout && !isReadonlyForm && readOnlyTextFieldStyles.inEnabledFormBase,
    as === 'textarea' &&
      isFormLayout &&
      !isReadonlyForm &&
      readOnlyTextFieldStyles.textareaInEnabledFormBase,
    isFormLayout &&
      !isReadonlyForm &&
      (hasInsideLabel
        ? readOnlyTextFieldStyles.inEnabledFormLabelInside
        : readOnlyTextFieldStyles.inEnabledFormNoLabelInside),
    as === 'textarea' &&
      isFormLayout &&
      !isReadonlyForm &&
      hasInsideLabel &&
      readOnlyTextFieldStyles.textareaInEnabledFormLabelInside,
    as !== 'textarea' && // not for textarea element
      isFormLayout &&
      !isReadonlyForm &&
      readOnlyTextFieldStyles.inEnabledFormNoStartContent,
    styleInterpolationClasses
  ]);

  if (as === 'input') {
    return (
      <input
        aria-label={ariaLabelForReadonly}
        aria-labelledby={ariaLabelledby}
        autofocus={autoFocus}
        class={readonlyDivClasses}
        id={id}
        readonly={true}
        // @ts-ignore
        ref={elementRef}
        type={type}
        value={value}
      />
    );
  }
  if (as === 'textarea') {
    return (
      <textarea
        aria-label={ariaLabelForReadonly}
        aria-labelledby={ariaLabelledby}
        autofocus={autoFocus}
        class={readonlyDivClasses}
        id={id}
        readonly={true}
        // @ts-ignore
        ref={elementRef}
        rows={rows}>
        {value}
      </textarea>
    );
  }
  return (
    <div
      aria-label={ariaLabelForReadonly}
      aria-labelledby={ariaLabelledby}
      aria-readonly={true}
      autofocus={autoFocus}
      class={readonlyDivClasses}
      // @ts-ignore
      ref={elementRef}
      role="textbox"
      tabIndex={0}>
      {value}
    </div>
  );
}
